.bikes {
  &__photo {
    margin-bottom: 32px;
    width: 100%;
    cursor: pointer;

    transition: transform 0.3s cubic-bezier(.45,-0.49,.48,1.91);

    &:hover {
      transform: scale(1.1);
    }
  }

  &__paragraph {
    margin-bottom: 16px;
  }

  &__price {
    margin-bottom: 40;

    @include onTablet {
      margin-bottom: 48px;
    }

    @include onDesktop {
      margin-bottom: 0;
    }
  }
}
